chrome.runtime.onInstalled.addListener(() => {
  console.log('Email Phishing Scanner installed');
});

chrome.action.onClicked.addListener((tab) => {
  console.log('HELLOOOO');
  chrome.scripting.executeScript({
    target: { tabId: tab.id },
    function: checkGmailUrl
  });
});

async function checkGmailUrl() {
  const url = 'http://gmail.com';
  chrome.runtime.sendMessage({ action: 'checkUrl', url: url }, (response) => {
    if (response) {
      if (response.safe === false) {
        console.log(`Potential Phishing Link: ${url}`);
      } else if (response.safe === true) {
        console.log(`This link is safe: ${url}`);
      } else if (response.error) {
        console.error(`Error checking URL: ${url}`, response.error);
      }
    } else {
      console.error(`No response received from background script for URL: ${url}`);
    }
  });
}

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
  if (message.action === 'checkUrl') {
    checkUrlSafety(message.url)
      .then(isSafe => {
        sendResponse({ safe: isSafe });
      })
      .catch(error => {
        console.error('Error checking URL:', error);
        sendResponse({ error: 'Error checking URL' });
      });
    return true; // Keep the message channel open for sendResponse
  }
});

async function checkUrlSafety(url) {
  const apiKey = 'AIzaSyAtleXDB1YoCK1VB32lAFYMqB_PCQ3BUHE';
  const hashPrefix = getHashPrefix(url); // You need to implement this function to get the hash prefix
  const response = await fetch(`https://safebrowsing.googleapis.com/v5/hashes:search?key=${apiKey}&hashPrefixes=${hashPrefix}`);

  if (!response.ok) {
    throw new Error('Network response was not ok');
  }

  const responseBody = await response.json();
  // Implement your logic to determine if the URL is safe based on the response body
  return responseBody.matches && responseBody.matches.length > 0 ? false : true;
}

function getHashPrefix(url) {
  const crypto = require('crypto');
  const hash = crypto.createHash('sha256');
  hash.update(url);
  const fullHash = hash.digest('hex');
  return fullHash.substring(0, 4); // Use the first 4 bytes as the hash prefix
}
